import Image from "next/image";
import Link from "next/link";
import AuthForm from "../../components/input/AuthForm";
import LoginAuthForm from "../../components/input/LoginAuthForm";

export default function LoginPage() {
    return(
        <div className="flex">
                <main className="bg-[url('/login.jpg')] min-h-screen w-230 bg-cover flex items-end bg-center"></main>
                <div className="bg-white justify-items-start px-5">
                    <div className=""><Link href="#" className="py-5">Logo</Link></div>
                    <div className="justify-items-start mt-5">
                        <p className="text-3xl font-extrabold text-[#028174] ">Log in to your account</p>
                        <p>Don't have an account? <Link href="/signup" className="font-medium text-[#028174] text-lg hover:underline">Sign up</Link></p>
                    </div>

                    <div >
                        <Link href="#" className="flex items-center justify-center gap-3 rounded border mt-6 mb-12 w-90 py-3 hover:cursor-pointer transition-all shadow-sm hover:shadow hover:rounded-4xl">
                            <Image src="/google.png" alt="Trusto logo" width={25} height={25} className="rounded-lg"/>
                            <p className="px-1.5">Signup with Google</p>
                        </Link>
                    </div>

                    <div className="w-110">
                        <AuthForm type="text" name="name" lable="Full Name*"/>
                        <AuthForm type="email" name="email" lable="Email address*"/>
                        <AuthForm type="password" name="password" lable="Password*"/>
                    </div>

                    <div className="flex items-center justify-center">
                        <button className="bg-[#0AB68B] text-white hover:bg-[#028174] font-bold py-3 px-4 rounded hover:cursor-pointer transition-all shadow-sm hover:shadow hover:rounded-4xl w-90">Log in to your Trusto account</button>
                        <br/>
                    </div>
                </div>
            </div>
    );
};